@import url("https://fonts.googleapis.com/css2?family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap");
:root {
  --gray-500: #b8b6b4;
  --gray-600: #1B2838;
  --gray-800: #171a21;
}

* {
  font-family: "PT Sans", sans-serif;
}

html {
  background-color: var(--gray-800);
  font-size: 87.5%;
}
@media (min-width: 740px) {
  html {
    font-size: 93.75%;
  }
}

body {
  --webkit-font-smoothing: antialiased;
}

.container {
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 1140px) {
  .container {
    max-width: 1140px;
  }
}

/*
    Este documento foi desenvolvido com base no mesmo contido em: https://meyerweb.com/eric/tools/css/reset/
 */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.swiper {
  width: auto;
  height: 300px;
}
.swiper .swiper-button-prev:after, .swiper .swiper-rtl .swiper-button-next:after,
.swiper .swiper-button-next:after, .swiper .swiper-rtl .swiper-button-prev:after {
  color: #fff;
}
.swiper .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  background-color: #fff;
}

:root {
  --background-top-header: var(--gray-600);
  --font-brand-color: var(--gray-500);
  --font-menu-color: var(--gray-500);
}

.header {
  align-items: center;
  background-color: var(--background-top-header);
  display: flex;
  padding: 2.35rem 0;
}
@media (min-width: 740px) {
  .header {
    height: 5rem;
    padding: 0;
  }
}
.header__top-bar {
  padding: 0 0.85rem;
  display: grid;
  gap: 0.5rem;
  grid-template-columns: 1fr;
}
@media (min-width: 740px) {
  .header__top-bar {
    grid-template-columns: 1fr 2fr 1fr;
  }
}
.header__top-bar h1 {
  color: var(--font-brand-color);
  font-size: 1.2rem;
  text-align: center;
}
@media (min-width: 740px) {
  .header__top-bar h1 {
    text-align: left;
    font-size: 3.5rem;
  }
}
.header__top-bar-menu {
  align-items: center;
  display: flex;
  justify-content: center;
}
.header__top-bar-menu ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.header__top-bar-menu ul li {
  color: var(--font-menu-color);
  margin: 0 0.25rem;
}
.header__top-bar-icons {
  align-items: center;
  display: flex;
  justify-content: center;
}
@media (min-width: 740px) {
  .header__top-bar-icons {
    justify-content: flex-start;
  }
}
.header__top-bar-icons p {
  color: var(--font-menu-color);
}

:root {
  --background-circle: var(--gray-500);
}

.home__circles {
  display: flex;
  align-items: center;
  height: 12rem;
}
.home__circles-content {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  width: 100%;
  gap: 0.25rem;
}
.home__circles-content-area {
  background-color: var(--background-circle);
  border-radius: 500px;
  height: 70px;
  width: 70px;
  text-align: center;
}

:root {
  --background: var(--gray-600);
  --font-color-text: var(--gray-500);
}

.home__about {
  background-color: var(--background);
  height: 25rem;
}
.home__about-row {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 0 0.85rem;
}
.home__about-row-text {
  margin-top: 5rem;
}
.home__about-row-text h2 {
  font-size: 2.3rem;
  margin: 0.6rem 0;
}
.home__about-row-text h3 {
  font-size: 0.95rem;
  margin: 0;
}
.home__about-row-text p {
  font-size: 1rem;
  line-height: 1.25rem;
}
.home__about-row-text h2,
.home__about-row-text h3,
.home__about-row-text p {
  color: var(--font-color-text);
}

.home__products {
  height: 45rem;
  padding: 0.75rem 0;
}
.home__products-left-card {
  width: 19.58rem;
}
.home__products-left-card-text {
  color: var(--gray-500);
  display: none;
  padding: 0 0.65rem;
  position: relative;
  top: -18rem;
}
.home__products-left-card-text h3 {
  font-size: 1.35rem;
  margin-bottom: 0.35rem;
}
.home__products-left-card-text p {
  font-size: 1.2rem;
}
.home__products-left-card img {
  height: 32rem;
  border-radius: 0.25rem;
  transition: all 0.5s;
}
.home__products-left-card img:hover {
  transform: scale(0.95);
  filter: brightness(0.25);
}
.home__products-left-card img:hover + .home__products-left-card-text {
  display: block;
}

:root {
  --footer-color: var(--gray-500);
  --background-button: var(--gray-800);
}

.footer__bar-third-social .instagram-icon, .footer__bar-third-social .facebook-icon {
  height: 2rem;
  transition: 0.5s;
  margin: 0.35rem 0;
}

.footer__bar-third-social .instagram-icon:hover, .footer__bar-third-social .facebook-icon:hover {
  cursor: pointer;
  filter: brightness(0.55);
}

.footer {
  padding-top: 1.5rem;
}
.footer__bar {
  padding: 0 0.85rem;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  gap: 0.65rem;
}
.footer__bar h2 {
  font-size: 2rem;
  color: var(--footer-color);
}
.footer__bar-first-menu {
  margin-top: 0.75rem;
}
.footer__bar-first-menu ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.25rem;
}
.footer__bar-first-menu ul li {
  color: var(--footer-color);
  font-size: 1rem;
}
.footer__bar-second-input {
  margin-top: 0.75rem;
  padding-bottom: 0.85rem;
  border-bottom: 1px solid var(--footer-color);
}
.footer__bar-second-input form {
  width: 100%;
}
.footer__bar-second-input #email {
  background-color: transparent;
  border: 0;
  color: var(--footer-color);
  font-size: 1rem;
  width: 78%;
}
.footer__bar-second-input #buttonSubmit {
  background-color: var(--background-color);
  border: 1px solid var(--footer-color);
  border-radius: 0.25rem;
  color: var(--footer-color);
  transition: filter 0.2s;
  width: 20%;
  padding: 0.25rem 0;
  font-weight: 700;
  font-size: 1rem;
}
.footer__bar-second-input #buttonSubmit:hover {
  cursor: pointer;
  filter: brightness(0.5);
}
.footer__bar-third-social {
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-top: 0.75rem;
}
.footer__bar-third-social .facebook-icon:hover {
  transform: scale(1.5) rotate(-25deg);
}
.footer__bar-third-social .instagram-icon:hover {
  transform: scale(1.5) rotate(25deg);
}
.footer__vnda {
  padding-top: 1.5rem;
}
.footer__vnda p {
  color: var(--gray-500);
  text-align: center;
}
.footer__my {
  padding: 1.5rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer__my p {
  font-size: 0.95rem;
  color: var(--footer-color);
}
.footer__my p a {
  color: currentColor;
  text-decoration: none;
  transition: filter 0.2s;
}
.footer__my p a:hover {
  filter: brightness(0.5);
}
.footer__my img {
  height: 1rem;
  margin: 0 0.25rem;
}